import React, { Component } from 'react';
import { connect} from 'react-redux';

class Player extends Component {
  render(){
    const {x, y} = this.props;
    return (
      <div
        style={{
          boxShadow: '0 0 10px 1px #600 inset, 0px 100px 5px 2px rgba(0,0,0,0.1)',
          borderRadius: '15px',
          height: '15px',
          left: ((x * 64) + (y * 64) + 58) + 'px',
          position: 'absolute',
          bottom: -((x * -32) + (y * 32) - 138) + 'px',
          width: '15px',
          zIndex: 10,
        }}
      />
    );
  }
}

const mapStateToProps = (state, { player }) => {
  const loc = state.locations.find(loc => loc.player);
  const {x, y} = loc;
  return ({
    x,
    y,
  });
};

export default connect(mapStateToProps)(Player);



