import React, { Component } from 'react';
import tower_04 from '../imgs/tower_04.png';
import tower_32 from '../imgs/tower_32.png';
import tower_36 from '../imgs/tower_36.png';

import tower_01 from '../imgs/tower_01.png';
import tower_44 from '../imgs/tower_44.png';
import tower_24 from '../imgs/tower_24.png';
import { Popover } from 'react-bootstrap';

class Tower extends Component {
  constructor(props){
    super(props);
    this.state = {hovering: false, popoverOpen: false};
  }

  render(){
    const {x, y, height} = this.props;
    const {hovering, popoverOpen} = this.state;
    return(
      <div>
      <div
        style={{
        left: ((x * 64) + (y * 64) + 19) + 'px',
        position: 'absolute',
        top: ((x * -32) + (y * 32) - 17 - (height * 16)) + 'px',
        zIndex: 3,}}
      >
        <Popover
          id="popover-basic"
          placement="right"
          positionLeft={70}
          positionTop={-300}
          title="Popover right"
          style={{
            display: hovering ? 'block' : 'none',
            height: '400px',
            width: '300px',
          }}
        >
          Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
        </Popover>
      </div>
      <img
        src={tower_01}
        style={{
          left: ((x * 64) + (y * 64) + 18) + 'px',
          opacity: 0.9,
          pointerEvents: 'none',
          position: 'absolute',
          bottom: (0 - ((x * -32) + (y * 32) - 24)) + 'px', //17
          WebkitFilter: hovering ? 'brightness(130%) hue-rotate(180deg)' : 'hue-rotate(0deg)',
          zIndex: 1,
        }}
      />
      <img
        src={tower_44}
        style={{
          left: ((x * 64) + (y * 64) + 25) + 'px',
          opacity: 0.9,
          pointerEvents: 'none',
          position: 'absolute',
          bottom: (0 - ((x * -32) + (y * 32) - 59)) + 'px', // 48
          WebkitFilter: hovering ? 'brightness(130%) hue-rotate(180deg)' : 'hue-rotate(0deg)',
          zIndex: 1,
        }}
      />
      <img
        src={tower_24}
        style={{
          left: ((x * 64) + (y * 64) + 20) + 'px',
          opacity: 0.9,
          pointerEvents: 'none',
          position: 'absolute',
          bottom: (0 - ((x * -32) + (y * 32) - 90)) + 'px', //88
          WebkitFilter: hovering ? 'brightness(70%) hue-rotate(180deg)' : 'hue-rotate(0deg)',
          zIndex: 1,
        }}
      />
      <div
        style={{
          backgroundColor: 'red',
          cursor: 'pointer',
          height: '64px',
          left: ((x * 64) + (y * 64)) + 'px',
          opacity: 0,
          pointerEvents: 'none',
          position: 'absolute',
          bottom: -((x * -32) + (y * 32)) + 'px',
          transform: 'rotate(0deg) scaleY(0.5) skew(45deg, -45deg) translate(32px, 0px)',
          width: '64px',
          zIndex: 2,
        }}
        onMouseEnter={() => this.setState({hovering: true})}
        onMouseLeave={() => this.setState({hovering: false})}
        onClick={() => this.setState({popoverOpen: true})}
      />
      <div
        style={{
          backgroundColor: 'red',
          cursor: 'pointer',
          height: '100px',
          left: ((x * 64) + (y * 64)) + 'px',
          opacity: 0,
          pointerEvents: 'none',
          position: 'absolute',
          bottom: -((x * -32) + (y * 32)) + 'px',
          transform: 'translate(21px, -54px)',
          width: '90px',
          zIndex: 2,
        }}
        onMouseEnter={() => this.setState({hovering: true})}
        onMouseLeave={() => this.setState({hovering: false})}
        onClick={() => this.setState({popoverOpen: true})}
      />
      <div
        style={{
          backgroundColor: 'red',
          cursor: 'pointer',
          height: '46px',
          left: ((x * 64) + (y * 64)) + 'px',
          opacity: 0,
          pointerEvents: 'none',
          position: 'absolute',
          bottom: -((x * -32) + (y * 32) - 99) + 'px',
          transform: 'rotate(0deg) scaleY(0.5) skew(45deg, -45deg) translate(49px, -5px)',
          width: '46px',
          zIndex: 2,
        }}
        onMouseEnter={() => this.setState({hovering: true})}
        onMouseLeave={() => this.setState({hovering: false})}
        onClick={() => this.setState({popoverOpen: true})}
      />
      </div>
    );
  }
}

export default Tower;
