import React from 'react';
import ReactDOM from 'react-dom';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import rootReducer from './reducers/rootReducer';
import App from './components/App';
import getRandomUnoccupiedLocationNotOnEdge from './selectors/getRandomUnoccupiedLocationNotOnEdge';
import './index.css';

const store = createStore(rootReducer);

for (let i = 0; i < 15; i++) {
  const location = getRandomUnoccupiedLocationNotOnEdge(store.getState());
  const {x, y} = location;
  const ranNumBt1And4 = Math.ceil(Math.random() * 4);
  store.dispatch({
    type: 'SPAWN_RESOURCE',
    resourceType: 'crystals',
    img: ranNumBt1And4,
    x,
    y,
  });
}

for (let i = 0; i < 15; i++) {
  const location = getRandomUnoccupiedLocationNotOnEdge(store.getState());
  const {x, y} = location;
  const ranNumBt1And8 = Math.ceil(Math.random() * 4);
  store.dispatch({
    type: 'SPAWN_RESOURCE',
    resourceType: 'rocks',
    img: ranNumBt1And8,
    x,
    y,
  });
}

store.dispatch({
  type: 'SPAWN_PLAYER',
  playerId: 'zak',
  x: 2,
  y: 0,
});

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById('root')
);

window.addEventListener('keydown', e => {
  if (e.which === 37) {
    e.preventDefault();
    store.dispatch({
      type: 'MOVE_PLAYER',
      playerId: 'zak',
      direction: 'left',
    });
  }

  if (e.which === 38) {
    e.preventDefault();
    store.dispatch({
      type: 'MOVE_PLAYER',
      playerId: 'zak',
      direction: 'up',
    });
  }

  if (e.which === 39) {
    e.preventDefault();
    store.dispatch({
      type: 'MOVE_PLAYER',
      playerId: 'zak',
      direction: 'right',
    });
  }

  if (e.which === 40) {
    e.preventDefault();
    store.dispatch({
      type: 'MOVE_PLAYER',
      playerId: 'zak',
      direction: 'down',
    });
  }

});



