export default (state) => {
  const unoccupiedLocationsNotOnEdge = state.locations.filter(loc =>
    !loc.resource
    && !loc.tower
    && !loc.player
    && loc.x !== 0
    && loc.y !== 0
    && loc.x !== 29
    && loc.y !== 29
  );

  const randomIndex = Math.floor(Math.random() * unoccupiedLocationsNotOnEdge.length);
  return unoccupiedLocationsNotOnEdge[randomIndex];
}



