const initialState = [];

for (let y = 0; y < 30; y++){
  for (let x = 29; x >= 0; x--) {
    initialState.push({
      id: (y * 30) + x,
      x,
      y,
      player: false,
      resource: false,
      tower: false,
    });
  }
}

initialState[27].tower = {id: 'tower1', playerId: 'zak'};
initialState[24].tower = {id: 'tower1', playerId: 'zak'};

export default (state = initialState, action) => {
  if (action.type === 'SPAWN_RESOURCE') {
    return state.map(loc => {
      if (loc.x === action.x && loc.y === action.y) {
        return Object.assign(
          {},
          loc,
          { resource: {type: action.resourceType, img: action.img, quantity: 1000} }
        );
      } else return loc;
    })
  }

  if (action.type === 'SPAWN_PLAYER') {
    return state.map(loc => {
      if (loc.x === action.x && loc.y === action.y) {
        return Object.assign(
          {},
          loc,
          { player: {id: action.playerId} }
        );
      }
      else return loc;
    });
  }

  if (action.type === 'MOVE_PLAYER') {
    const oldLoc = state.find(loc => loc.player.id === action.playerId);
    const oldX = oldLoc.x;
    const oldY = oldLoc.y;

    let newX = oldX, newY = oldY;
    if (action.direction === 'left') newX = oldX - 1;
    else if (action.direction === 'up') newY = oldY - 1;
    else if (action.direction === 'right') newX = oldX + 1;
    else if (action.direction === 'down') newY = oldY + 1;

    return state.map(loc => {
      if (loc === oldLoc) {
        return Object.assign(
          {},
          loc,
          { player: false }
        );
      }
      else if (loc.x === newX && loc.y === newY) {
        return Object.assign(
          {},
          loc,
          { player: {id: action.playerId} }
        );
      }
      else return loc;
    })
  }

  if (action.type === 'BUILD_TOWER') {
    return state.map(loc => {
      if (loc.x === action.x && loc.y === action.y) {
        return Object.assign(
          {},
          loc,
          { tower: {id: action.playerId} }
        );
      }
      else return loc;
    });
  }

  return state;
}



