import React, { Component } from 'react';
import { connect } from 'react-redux';
import { StyleSheet, css } from 'aphrodite';
import Tower from './Tower';

const styles = StyleSheet.create({
  hover: {
    ':hover': {
      opacity: '0.25'
    }
  },
});

class Terrain extends Component {
  render(){
    const {x, y, resource, tower} = this.props.location;

    let height = 0;
    let src = '';

    if (resource === false) src = 'landscape_21';
    else {
      src = resource.type + '_' + resource.img;
      height = -1;
    }

    return (
      <div>
        <img
          src={require(`../imgs/${src}.png`)}
          style={{
            left: ((x * 64) + (y * 64)) + 'px',
            pointerEvents: 'none',
            position: 'absolute',
            bottom: (0 - ((x * -32) + (y * 32) - (16 * height))) + 'px',
            WebkitFilter: 'hue-rotate(0deg)',
            zIndex: 1,
          }}
        />

      {
        tower
        ?
          <Tower x={x} y={y} height={0}/>
        :
        <div
          className={css(styles.hover)}
          style={{
            backgroundColor: '#0f0',
            cursor: 'pointer',
            height: '64px',
            left: ((x * 64) + (y * 64)) + 'px',
            opacity: 0,
            position: 'absolute',
            bottom: -((x * -32) + (y * 32)) + 'px',
            transform: 'rotate(0deg) scaleY(0.5) skew(45deg, -45deg) translate(32px, 0px)',
            width: '64px',
            zIndex: 1,
          }}
          onClick={() => this.props.dispatch({
            type: 'BUILD_TOWER',
            playerId: 'zak',
            x,
            y,
          })}
        />
      }
      </div>
    );
  }
}

export default connect()(Terrain);



